import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';

//router
import { HashRouter as Router, Route, Switch } from 'react-router-dom'

// components
import UseStateBasics from './components/usestate'
import UseStateArray from './components/usestatearry'
import UseStateObject from './components/usestateobject'
import UseStateCounter from './components/usestatecounter'
import UseEffectBasics from './components/useeffect'
import UseEffectMultiple from './components/useeffectmultiple'
import Operators from './components/operators'
import Form from './components/form'
import UseRefBasics from './components/useref'
import UseReducerBasics from './components/usereducer'
import PropDrilling from './components/propdrilling'
import ContextApi from './components/contextapi'
import Example from './components/call_custom_hook'

ReactDOM.render(
  <React.StrictMode>
    <Router>
      <Switch>
        <Route exact path="/">
          <App />
        </Route>

        <Route path="/react-hooks-example/#/usestate">
          <div className='container'>
            <h2>UseState Basic Example</h2>
          </div>
          <UseStateBasics />
        </Route>

        <Route path="/react-hooks-example/#/usestatearray">
          <div className='container'>
            <h2>UseState array example</h2>
          </div>
          <UseStateArray />
        </Route>

        <Route path="/react-hooks-example/#/usestateobject">
          <UseStateObject />
        </Route>

        <Route path="/react-hooks-example/#/usestatecounter">
          <UseStateCounter />
        </Route>

        <Route path="/react-hooks-example/#/useseffect">
          <UseEffectBasics />
        </Route>

        <Route path="/react-hooks-example/#/useseffectmulti">
          <UseEffectMultiple />
        </Route>

        <Route path="/react-hooks-example/#/operators">
          <Operators />
        </Route>

        <Route path="/react-hooks-example/#/form">
          <Form />
        </Route>

        <Route path="/react-hooks-example/#/userefbasics">
          <UseRefBasics />
        </Route>

        <Route path="/react-hooks-example/#/usereducer">
          <UseReducerBasics />
        </Route>

        <Route path="/react-hooks-example/#/propdrilling">
          <PropDrilling />
        </Route>

        <Route path="/react-hooks-example/#/contextapi">
          <ContextApi />
        </Route>

        <Route path="/react-hooks-example/#/customhook">
          <Example />
        </Route>

        {/* when no routes matched root page will render always */}
        <Route path="*">
          <App />
        </Route>
      </Switch>
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
);
